@import url("https://fonts.googleapis.com/css?family=Special+Elite&display=swap");
.container-form {
  width: 100%;
  height: 7em;
  position: fixed;
  background-image: url("../../img/trueno.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 10px -6px #222;
  font-family: 'Special Elite', cursive; }
  .container-form input {
    height: 3rem;
    border-radius: 0.5rem;
    border: 1px solid white;
    padding: 10px;
    margin: 10px;
    background: rgba(0, 0, 0, 0.3); }
  .container-form button {
    color: white;
    border: none;
    background: none;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    line-height: 45px;
    position: absolute;
    margin: 10px;
    font-family: 'Special Elite', cursive; }
  .container-form ::placeholder {
    color: white;
    opacity: 0.5;
    font-family: 'Special Elite', cursive;
    font-size: 16px; }

.btn-2 {
  letter-spacing: 0; }

.btn-2:hover,
.btn-2:active {
  letter-spacing: 2px; }

.btn-2:after,
.btn-2:before {
  backface-visibility: hidden;
  border: 1px solid rgba(255, 255, 255, 0);
  bottom: 0px;
  content: " ";
  display: block;
  margin: 0 auto;
  position: relative;
  transition: all 280ms ease-in-out;
  width: 0; }

.btn-2:hover:after,
.btn-2:hover:before {
  backface-visibility: hidden;
  border-color: #fff;
  transition: width 350ms ease-in-out;
  width: 90%; }

.btn-2:hover:before {
  bottom: auto;
  top: 0;
  width: 90%; }

.container-results {
  padding: 8em 0; }
